<template>
    <header class="app-header navbar">
        <button class="navbar-toggler mobile-sidebar-toggler d-lg-none" type="button" @click="mobileSidebarToggle">
            <span class="navbar-toggler-icon"></span>
        </button>
        <b-link class="navbar-brand" to="#">Cheats</b-link>
        <button class="navbar-toggler sidebar-toggler d-md-down-none" type="button" @click="sidebarToggle">
            <span class="navbar-toggler-icon"></span>
        </button>
        <b-navbar-nav class="d-md-down-none">
            <b-nav-item class="px-3">Dashboard</b-nav-item>
            <b-nav-item class="px-3"><router-link :to="{ name: 'Users'}">Users</router-link></b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
            <HeaderDropdown></HeaderDropdown>
        </b-navbar-nav>
    </header>
</template>
<script>
    import { isLoggedIn, userRole } from '../utils/auth'
    import HeaderDropdown from './HeaderDropdown.vue'
    import { getNames } from '../_nav.js'
    export default {
        name: 'header',
        data () {
            return {
                roles: ''
            }
        },
        components: {
            HeaderDropdown
        },
        mounted () {
            if (isLoggedIn()) {
                this.getUserRole()
            }
            getNames()
        },
        methods: {
            sidebarToggle (e) {
                e.preventDefault()
                document.body.classList.toggle('sidebar-hidden')
            },
            sidebarMinimize (e) {
                e.preventDefault()
                document.body.classList.toggle('sidebar-minimized')
            },
            mobileSidebarToggle (e) {
                e.preventDefault()
                document.body.classList.toggle('sidebar-mobile-show')
            },
            asideToggle (e) {
                e.preventDefault()
                document.body.classList.toggle('aside-menu-hidden')
            },
            // get the current user role
            getUserRole () {
                userRole().then((result) => {
                    this.role = result
                }).catch((err) => {
                    console.log(err.toString())
                })
            }
        }
    }
</script>



